import fs from 'fs';
import path from 'path';

const getFilePath = (filename) => path.join(process.cwd(), 'data', filename);
const ensureDir = () => {
  const dir = path.join(process.cwd(), 'data');
  if (!fs.existsSync(dir)) {
    fs.mkdirSync(dir, { recursive: true });
  }
};

export async function GET(request) {
  try {
    ensureDir();
    const searchParams = new URL(request.url).searchParams;
    const action = searchParams.get('action');

    if (action === 'list') {
      const accommodationsPath = getFilePath('accommodations.json');
      
      if (!fs.existsSync(accommodationsPath)) {
        const defaultAccommodations = [
          { id: 1, name: 'Deluxe Room', type: 'room', price_per_night: 150, capacity: 2, created_at: new Date().toISOString() },
          { id: 2, name: 'Standard Room', type: 'room', price_per_night: 100, capacity: 2, created_at: new Date().toISOString() },
          { id: 3, name: 'Suite', type: 'suite', price_per_night: 250, capacity: 4, created_at: new Date().toISOString() },
        ];
        fs.writeFileSync(accommodationsPath, JSON.stringify(defaultAccommodations, null, 2));
        return Response.json({ data: defaultAccommodations });
      }

      const accommodations = JSON.parse(fs.readFileSync(accommodationsPath, 'utf-8') || '[]');
      return Response.json({ data: accommodations });
    }

    if (action === 'get') {
      const id = searchParams.get('id');
      const accommodationsPath = getFilePath('accommodations.json');
      
      if (!fs.existsSync(accommodationsPath)) {
        return Response.json({ error: 'Not found' }, { status: 404 });
      }

      const accommodations = JSON.parse(fs.readFileSync(accommodationsPath, 'utf-8') || '[]');
      const accommodation = accommodations.find((a) => a.id === parseInt(id || '0'));

      if (!accommodation) {
        return Response.json({ error: 'Not found' }, { status: 404 });
      }

      return Response.json(accommodation);
    }

    return Response.json({ error: 'Invalid action' }, { status: 400 });
  } catch (error) {
    console.error('Accommodation error:', error);
    return Response.json({ error: 'Failed to fetch accommodations' }, { status: 500 });
  }
}

export async function POST(request) {
  try {
    ensureDir();
    const searchParams = new URL(request.url).searchParams;
    const action = searchParams.get('action');

    if (action === 'create') {
      const body = await request.json();
      const accommodationsPath = getFilePath('accommodations.json');

      let accommodations = [];
      if (fs.existsSync(accommodationsPath)) {
        accommodations = JSON.parse(fs.readFileSync(accommodationsPath, 'utf-8') || '[]');
      }

      const accommodationId = accommodations.length > 0 ? Math.max(...accommodations.map((a) => a.id || 0)) + 1 : 1;

      const newAccommodation = {
        id: accommodationId,
        name: body.name || '',
        type: body.type || 'room',
        price_per_night: parseFloat(body.price_per_night || '0'),
        capacity: parseInt(body.capacity || '2'),
        created_at: new Date().toISOString(),
      };

      accommodations.push(newAccommodation);
      fs.writeFileSync(accommodationsPath, JSON.stringify(accommodations, null, 2));

      return Response.json({ success: true, ...newAccommodation });
    }

    return Response.json({ error: 'Invalid action' }, { status: 400 });
  } catch (error) {
    console.error('Accommodation create error:', error);
    return Response.json({ error: 'Failed to create accommodation' }, { status: 500 });
  }
}
